.app_header{
    background: rgb(18, 140, 126);
    padding: 10px;
    position: sticky;
    top: 0;
    z-index: 1;
}
.logo_section{
    display: flex;
    justify-content: space-between;
    padding: 5px 40px;
}
.app_icon{
    color: #fff;
    font-size: 25px;
    cursor: pointer;
}
.app_name{
    color: var(--mainWhite);
}
.app_menu{
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    align-items: center;
}
.list_items ul{
    display: flex;
    margin-top: 16px;
}
.list_items ul li{
    padding-right: 25px;
}
.list_items li a{
    color: var(--mainWhite);
}
.list_items li a:hover{
    text-decoration: none;
    color: rgb(52, 183, 241);
}
.app_menu .list_items .active{
    color: rgb(236, 229, 221);
}
.seach_icon{
    position: relative;
}
.app_search{
    position: absolute;
    color: rgb(18, 140, 126);
    top: 5px;
    right: 5px;
}
.search_text{
    border: none;
    outline: none;
    border-radius: 5px;
    padding: 2px 10px;
}
.mobile_search{
    position: relative;
    display: flex;
    justify-content: center;
    display: none;
}
.app_notifications{
    position: absolute;
    top: 8px;
    right: 28px;
    background: var(--offWhite);
    border-radius: 45px !important;
    width: 25px;
    text-align: center;
    color: var(--primaryColor);
}
@media (max-width: 768px){
    .app_menu{
        display: none;
    }
    .mobile_search{
        display: flex;
        padding: 10px 0;
    }
    .logo_section{
        padding: 5px 10px;
    }
}