.form-section{
    max-width: 350px;
    margin: auto;
    padding: 30px;
}
.app_forminner{
    border: 1px solid var(--primaryColor);
    border-radius: 5px;
    padding: 30px;
    background: var(--offWhite);
}
.text-form_head{
    font-family: var(--fontFamily);
    font-weight: normal;
    text-transform: capitalize;
    letter-spacing: var(--mainSpacing);
    font-size: 18px;
    text-align: center;
    padding-bottom: 10px;
}
.form_label{
    font-family: var(--fontFamily);
    font-size: 14px;
    letter-spacing: var(--headSpacing);
}
.form_input{
    box-shadow: none !important;
    letter-spacing: var(--headSpacing);
    font-family: var(--fontFamily);
}
.submit_button{
    width: 100%;
    background-color: var(--primaryColor) !important;
    border: none !important;
    box-shadow: none !important;
}
.login_footer{
    margin: 15px 0 0 5px;
    font-family: var(--fontFamily);
    letter-spacing: var(--headSpacing);
    text-transform: capitalize;
    font-size: 14px;
}
.login_footer button{
    border: none;
    color: var(--primaryColor);
    padding: 0 0 0 10px;
    background: none;
    outline: none !important;
}
.login_footer button:hover{
    color: var(--primaryDarkColor);
}