.card{
    transition: var(--mainTransition);
    height: 100%;
    position: relative;
}
.img-container{
    position: relative;
}
.card-body{
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
}
.view_products{
    position: absolute;
    top: 10%;
    right: 0;
    transform: translate(-50%, -50%);
}
.eye_icon{
    font-size: 25px;
    opacity: 0;
    color: var(--primaryColor);
}
.card:hover .eye_icon{
    opacity: 1;
}

.app_products .products-container .product-card, .app_dayoffers .dayprod-container .product-card
,.app_offers .shopoferprod .product-card{
    background: #E8F5E9;
    border: none;
    margin: 5px;
    padding: 5px;
}
.products-container .banner-sideprod .banner-products{
    padding: 0;
    margin: 0px 0 10px 0;
}
.product-section{
    padding: 40px 0 0 0;
}
.products-container{
    padding: 25px 0 25px 0;
}
.img-container, .prod-name{
    margin: auto;
}
.proddetails{
    padding: 5px 10px 5px 10px;
    display: flex;
    justify-content: space-between;
}
.purchase-price{
    margin: auto;
}
.stike-price{
    color: var(--mainRed);
}
.prod-icon{
    color: var(--primaryColor);
    cursor: pointer;
}
.sold-out{
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--mainRed);
    width: 115px;
    height: 35px;
    border-bottom-right-radius: 25px;
    text-align: center;
    padding: 7px;
    color: var(--mainWhite);
    text-transform: capitalize;
}
.prod-name{
    text-transform: capitalize;
    font-size: 14px;
    font-weight: bold;
}
.prod-price{
    text-transform: capitalize;
    font-weight: bold;
    font-size: 14px;
}