@import url(https://fonts.googleapis.com/css?family=Catamaran:300,400|Kaushan+Script&display=swap);
:root {
  --primaryColor: rgb(18, 140, 126);
  --primaryDarkColor: #c02c03;
  --primaryFont: "Catamaran", sans-serif;
  --slantedFont: "Kaushan Script", cursive;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBackground: #ececec;
  --mainOverlay: rgba(35, 10, 36, 0.4);
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #afafaf;
  --mainRed: #dc3545;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 0.3rem;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  --mainBorderRadius: 0.25rem;
  --maxWidth: 40rem;
  --smallWidth: 85vw;
  --fullWidth: 1170px;
  --fontFamily: 'Montserrat', sans-serif;
  --headSpacing: 0.05rem;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  font-family: "Catamaran", sans-serif !important;
  font-family: var(--primaryFont) !important;
  color: #222 !important;
  color: var(--mainBlack) !important;
  background: #fff !important;
  background: var(--mainWhite) !important;
  background-color: #ececec;
  background-color: var(--mainGrey);
  line-height: 1.4 !important;
  font-size: 1rem !important;
  font-weight: 300 !important;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #222;
  color: var(--mainBlack);
}
/* Error Page */
.error{
  display: flex;
  justify-content: center;
  height: 68vh;
}
.error-container{
  text-align: center;
  text-transform: capitalize;
}
.section {
  padding: 4rem 0;
}

/* Review Page */

.confirmbtn{
  color: #fff;
  color: var(--mainWhite);
  background: #c02c03;
  background: var(--primaryDarkColor);
  border: none;
  padding: 5px 8px 5px 8px;
  text-transform: capitalize;
}

/* Products Page */

.page-btn{
  padding: 5px;
  margin: 2px;
  border: none;
  background: #dc3545;
  background: var(--mainRed);
  color: #fff;
  color: var(--mainWhite);
  font-family: "Catamaran", sans-serif;
  font-family: var(--primaryFont);
  outline: none;
}
.page-btn:focus{
  outline: none;
}
.pageleft-right{
  padding: 5px;
  background: #dc3545;
  background: var(--mainRed);
}
.page-icon{
  color: #fff;
  color: var(--mainWhite);
  font-size: 20px;
}
.pagbuttons button{
  width: 35px;
}
.currentpage{
  background: rgb(18, 140, 126);
  background: var(--primaryColor);
}
.search-failed{
  display: flex;
  justify-content: center;
  padding: 100px;
}

/* side nav */

.side-nav{
  width: 20%;
  height: 100%;
  position: fixed;
  z-index: 9;
  background: rgb(18, 140, 126);
  background: var(--primaryColor);
  display: none;
}
.open-nav{
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.close-nav{
  -webkit-transform: translateY(-150vh);
          transform: translateY(-150vh);
}
.list_itemsnav li a{
  color: #fff;
  color: var(--mainWhite);
  text-decoration: none;
}
.list_itemsnav li a:hover{
  color: #dc3545;
  color: var(--mainRed);
}
.list_itemsnav li a:focus{
  color: #dc3545;
  color: var(--mainRed);
}
.list_itemsnav li{
  padding: 5px 5px 5px 20px;
}


/* backdrop */
.back-drop{
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 8;
  left: 0;
  top: 0;
  background-color: rgba(0,0,0,0.5);
  cursor: pointer;
  display: none;
}

/* orders page */
.order-status{
  text-align: end;
}
.seller-details, .orderitems-quanity{
  display: flex;
  justify-content: space-between;
}
.viewmore{
  text-align: end;
  display: flex;
  justify-content: space-between;
}
.order-icon{
  position: relative;
  font-size: 20px;
  right: 35px;
  cursor: pointer;
  color: rgb(18, 140, 126);
  color: var(--primaryColor);
}
.ord-items{
  margin: 0;
}

/* wish list */

.wish-image{
  display: flex;
  justify-content: center;
}
.wish_img{
  height: 100px;
}
.prodwish-handle{
  display: flex;
  justify-content: space-between;
}
.prodwish-btn{
  border: none;
  background: rgb(18, 140, 126);
  background: var(--primaryColor);
  padding: 5px;
  width: 100px;
  color: #fff;
  color: var(--mainWhite);
  text-transform: capitalize;
  letter-spacing: 0.03rem;
  font-size: 15px;
  text-align: center;
  font-weight: normal;
  text-decoration: none;
}
.remov-btn{
  background: #dc3545;
  background: var(--mainRed);
}
.prodwish-btn:focus{
  outline: none;
}
.prodwish-btn:hover{
  background: #ececec;
  background: var(--mainGrey);
  color: rgb(18, 140, 126);
  color: var(--primaryColor);
  text-decoration: none;
}

@media (max-width: 768px){
  .side-nav{
    width: 50%;
    display: block;
  }
  .back-drop{
    display: block;
  }
}



