}
.model_conatainer{
  position: fixed;
  top: 30%;
  left: 50%;
  letter-spacing: var(--mainSpacing);
  border-radius: var(--mainBorderRadius);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: none;
  z-index: 9;
  padding: 40px 150px;
  background: var(--primaryColor);
  text-align: center;
  color: var(--mainWhite);
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: capitalize;
  
}
.alert_display{
    display: block;
}
.alert_bcg{
    background: var(--mainRed);
}
.alert_model p{
    margin-bottom: 0;
}
.model_close{
  color: var(--mainWhite);
  font-size: 22px;
  background: transparent;
  border: none;
  display: inline-block;
  position: absolute;
  top: 5px;
  right: 7px;
  cursor: pointer;
  line-height: 0;
  outline: none;
}
