.prod-name{
    text-transform: capitalize;
    letter-spacing: 0.1rem;
    font-family: var(--primaryFont);
}
.prod-price span p{
    padding: 10px 10px 10px 0;
    font-size: 15px;
    letter-spacing: 0.1rem;
    font-family: var(--primaryFont);
}
.prod-description{
    text-transform: capitalize;
    letter-spacing: 0.1rem;
    font-family: var(--primaryFont);
    font-size: 14px;
}
.prod-image{
    display: flex;
    justify-content: center;
}
.product-image{
    margin: auto;
}
.prod-price{
    display: flex;
}
.product-container{
    padding: 60px;
}
.product-image{
    width: 60%;
}
.prod-buttons{
    display: flex;
    justify-content: space-evenly;
}
.prod-description{
    height: 120px;
}
.prod-btn{
    border: none;
    margin: 5px 5px 5px 0;
    padding: 8px;
    background: var(--primaryColor);
    color: var(--mainWhite);
    text-transform: capitalize;
    font-size: 14px;
    font-family: var(--primaryFont);
}
.prod-btn:hover{
    background: var(--mainGrey);
    color: var(--primaryColor);
    text-decoration: none;
}