.text_titles{
    display: flex;
    justify-content: space-between;
}
.text-title_head{
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    text-transform: capitalize;
    letter-spacing: var(--mainSpacing);
    font-size: 14px;
  }
  .text-all_link{
    font-family: 'Montserrat', sans-serif;
    text-transform: capitalize;
    font-size: 15px;
    letter-spacing: 0.1rem;
  }
  .text-all_link:hover{
      text-decoration: none;
  }
  .all_icon{
      color: #007bff;
      font-size: 12px;
      margin: 0 0 0 5px;
      cursor: pointer;
  }