.cart-body{
    display: flex;
    justify-content: space-evenly;
}
.cart-image{
    display: flex;
    justify-content: center;
}
.cart_img{
    height: 120px;
}
.prod-handle{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
.prod-quantity{
    padding: 5px;
}
.cart-icon{
    font-size: 20px;
    color: var(--primaryColor);
    cursor: pointer;
}
.prod-remove{
    cursor: pointer;
    font-size: 20px;
    color: var(--mainRed);
}
.prod-headcart{
    display: flex;
    justify-content: space-between;
    padding: 5px;
}